<template>
    <div class="container back-one" v-if="gameList">

        <div class="transformation-page">
            <div class="money">
                <div class="fl">
                    <span>钱包余额：</span>
                    <h2>
                        <span class="c1">{{ UserBaseInfo.result==108 ? '未登录' : UserBaseInfo.balance >= 0 ? UserBaseInfo.balance : '加载中'}}</span>
                        <span v-if="UserBaseInfo.result!=108">元</span>
                        <i class="ico-down"></i>
                    </h2>
                </div>
                <div class="fr">
                    <!-- <h2>转账记录</h2> -->
                </div>
            </div>
            <div class="three-wrap">
                <ul>
                    <li :class="classHandle(item.gameCode)" v-for="item in gameList" :key="item.gameCode">
                        <div class="pict">
                            <img :src="importImg(item.gameCode)" alt="">
                        </div>
                        <h2>{{item.gameName}}</h2>
                        <p v-if="item.maintaining">维护中</p>
                        <p v-else>{{item.money}}</p>
                        <button v-if="item.maintaining">一键转入</button>
                        <button v-else @click="to_change(item.gameCode, true, true)">一键转入</button>
                    </li>
                </ul>
            </div>
            <div class="turn-out">
                <div :class="['turn-wrap',{'active': !is}]">
                    <div class="box fl">
                        <h2>转出钱包</h2>
                        <div class="item">
                            <p>我的钱包</p>
                            <span><i>{{ UserBaseInfo.result==108 ? '未登录' : UserBaseInfo.balance >= 0 ? UserBaseInfo.balance : '加载中'}}</i><var v-if="UserBaseInfo.result!=108">元</var></span>
                        </div>
                    </div>
                    <div @click="is=!is" class="ico-turn"></div>
                    <div class="box fr">
                        <h2>转入钱包</h2>
                        <div @click="show=true" class="item">
                            <p>{{actions[select].name}}</p>
                            <span>{{actions[select].price==='' ? '加载中' : actions[select].price==-1 ? actions[select].subname: `${actions[select].price}`}}</span>
                        </div>
                    </div>
                </div>
                <div class="trun-money">
                    <div class="box">
                        <span>转入金额：</span>
                        <input type="tel" @input="money_tabs=-1" v-model="money" placeholder="请输入金额">
                        <i v-if="money.length > 0" @click="money='';money_tabs=-1;" class="ico-clear"></i>
                    </div>
                </div>
                <div class="money-null">
                    <ul>
                        <li @click="money_addend(0)" :class="{acti: money_tabs==0}">全部</li>
                        <li @click="money_addend(1)" :class="{acti: money_tabs==1}">100</li>
                        <li @click="money_addend(2)" :class="{acti: money_tabs==2}">300</li>
                        <li @click="money_addend(3)" :class="{acti: money_tabs==3}">500</li>
                        <li @click="money_addend(4)" :class="{acti: money_tabs==4}">1000</li>
                    </ul>
                </div>
            </div>
            <div class="confirm">
                <button @click="to_ky">确定转换</button>
            </div>
        </div>

        <van-action-sheet
        v-model="show"
        :actions="actions"
        cancel-text="取消"
        @cancel="onCancel"
        @select="onSelect"
        />

        <!-- 下拉 -->
        <div class="my-card">
            <div class="layout">
                <div class="title">
                    <h2 class="fl">我的钱包</h2>
                    <div class="fr">
                        <span class="c1">{{ UserBaseInfo.result==108 ? '未登录' : UserBaseInfo.balance >= 0 ? UserBaseInfo.balance : '加载中'}}</span>
                        <span v-if="UserBaseInfo.result!=108">元</span>
                        <i @click="getUserBaseInfo(true)" class="ico-ref"></i>
                    </div>
                </div>
                <ul>
                    <!-- <li>
                        <h2>君博彩票</h2>
                        <span>{{actions[3].price==='' ? '加载中' : actions[0].price==-1 ? actions[0].subname: `${actions[0].price}元`}}<i @click="getAllAmount(true)" class="ico-ref"></i></span>
                    </li>
                    <li>
                        <h2>开元棋牌</h2>
                        <span>{{actions[0].price==='' ? '加载中' : actions[0].price==-1 ? actions[0].subname: `${actions[0].price}元`}}<i @click="getAllAmount(true)" class="ico-ref"></i></span>
                    </li>
                    <li>
                        <h2>VG棋牌</h2>
                        <span>{{actions[1].price==='' ? '加载中' : actions[1].price==-1 ? actions[1].subname: `${actions[1].price}元`}}<i @click="getVgAmount(true)" class="ico-ref"></i></span>
                    </li>
                    <li>
                        <h2>FG棋牌</h2>
                        <span>{{actions[2].price==='' ? '加载中' : actions[2].price==-1 ? actions[2].subname: `${actions[2].price}元`}}<i @click="getLyAmount(true)" class="ico-ref"></i></span>
                    </li> -->
                </ul>
            </div>
        </div>

        <!-- 转账记录 -->
        <div class="transfer-record">
            <div class="layout">
                <div class="close">
                    <h2>转账记录</h2>
                    <div class="fr">
                        <button>关闭</button>
                    </div>
                </div>
                <div class="tabs">
                    <span @click="toggleTabs(1)" :class="{acti:tabs==1}">今日</span>
                    <span @click="toggleTabs(2)" :class="{acti:tabs==2}">昨天</span>
                    <span @click="toggleTabs(3)" :class="{acti:tabs==3}">本月</span>
                </div>
                <div class="list" v-if="list">
                    <ul v-if="list.length > 0">
                        <li v-for="item in list" :key="item.pk">
                            <div class="title">
                                <span class="fl">{{ item.coinOperType | operType }}</span>
                                <span class="fr">{{item.money > 0 ? `+${item.money}` : `${item.money}`}}元</span>
                            </div>
                            <div class="time">
                                <span class="fl">{{item.createTime | timer}}</span>
                                <span class="fr">余额：{{item.leftCoin}}</span>
                            </div>
                        </li>
                    </ul>
                    <div v-else class="no-data">没有找到符合查询条件的记录</div>
                </div>
                <loadding v-else></loadding>
            </div>
        </div>

    </div>
</template>

<script>

    import { mapState } from "vuex";

    import $ from "jquery";

    import {
        GAME_GET_USER_BASE_INFO
    } from "@/tool/common/constants.js";

    import { getToday, lastDay, thisMonth, getTomorrow } from "@/tool/common/time.js";
    import { companyShortName } from "@/tool/common/config.js";

    export default {
        data(){
            return {

                tabs: 1,

                config: {
                    uid: '',
                    token:'',
                },

                is: true,
                money:'',

                add_list: ['', 100, 300, 500, 1000],
                money_tabs: -1,

                show: false,
                select: 0,

                ref: [],

                list:false,

                gameList: false,
            }
        },
        watch: {
            money(new_value){
                var arr = ''.concat(new_value).split('')
                var number = []
                arr.forEach((item, index)=>{
                    if(index==0 && item==0){
                        console.log('first not number 0');
                    }else{
                        if(/[0-9]/.test(item)){
                            number.push(item)
                        }
                    }
                })
                var result = number.join('')
                if(result > 1000000){
                    result = 1000000
                    this.$toast('最大100万')
                }
                this.money = result
            },
        },
        computed:{
            ...mapState('Game', {
                UserBaseInfo: state => state[GAME_GET_USER_BASE_INFO],
            }),
            actions(){
                var arr = []
                if(this.gameList){
                    this.gameList.forEach((item, index)=>{
                        let obj = {}
                        obj.name = item.gameName
                        obj.index = index
                        obj.price = item.money
                        obj.code = item.gameCode
                        if(item.maintaining){
                            obj.subname = '维护中'
                        }
                        arr.push(obj)
                    })
                }
                return arr
            }
        },
        created(){

            const {uid, token} = this.$route.query;
            this.config.uid = uid;
            this.config.token = token;

            this.getUserBaseInfo();
            this.getGameList();

        },
        mounted(){
            var _this = this;

            $('.transformation-page .money .fl h2').click(function(){
                $(this).toggleClass('acti')
                $('.my-card').slideToggle()
            })

            $('.ico-ref').click(function (e) {
                if(!($(this).hasClass('acti'))){
                    e.preventDefault();
                    $(this).addClass('acti')
                }
            });

            // 转账记录
            $('.transfer-record .layout .close .fr button').click(function(){
                $('.transfer-record').addClass('active')
                setTimeout(() => {
                    $('.transfer-record').fadeOut().removeClass('active')
                }, 800);
            })
            $('.transformation-page .money .fr h2').click(function(){
                $('.transfer-record').fadeIn()
                _this.getNetData()
            })

            $('.my-card').click(function(e){
                e.stopPropagation()
                $(this).slideUp()
                $('.transformation-page .money .fl h2').removeClass('acti')
            })

            $('.my-card .layout').click(function(e){
                e.stopPropagation()
            })
        },
        methods: {
            to_change(code, is, all=false){
                switch (code) {
                    case '1000001359742941':
                        return this.kaiyuan(is, all)
                    case '1000000470622605':
                        return this.vg(is, all)
                    case '1000001350416944':
                        return this.fg(is, all)
                    case '1000001350416946':
                        return this.junbo(is, all)
                    default:
                        return 0
                }
            },
            // 引入图片
            importImg(e){
                switch (e) {
                    case '1000001359742941':
                        return require('../../static/img/87e01f_105x46.png')
                    case '1000000470622605':
                        return require('../../static/img/9e6a89_68x44.png')
                    case '1000001350416944':
                        return require('../../static/img/d121c6_340x340.png')
                    case '1000001350416946':
                        return require('../../static/img/060748_330x283.png')
                    default:
                        return require('../../static/img/6ffe86_187x74.png')
                }
            },
            // 类名
            classHandle(e){
                switch (e) {
                    case '1000001359742941': // 开元棋牌
                        return 'c1'
                    case '1000000470622605': // VG棋牌
                        return 'c2'
                    case '1000001350416944': // FG棋牌
                        return 'c3'
                    case '1000001350416946': // 君博彩票
                        return 'c4'
                    default:
                        return 'c0'
                }
            },

            toggleTabs(tabs){
                this.tabs = tabs
                this.getNetData()
            },

            getNetData(){
                this.list = false
                var data = {
                    ...this.config,
                    pageIndex:1,
                    pageSize:20,
                    type:'',
                }
                switch (this.tabs) {
                    case 1:
                        data.startTime = getToday()
                        data.endTime = getTomorrow()
                        break;
                    case 2:
                        data.startTime = lastDay()
                        data.endTime = getToday()
                        break;
                    case 3:
                        var date = thisMonth()
                        data.startTime = date.thisMonthStart
                        data.endTime = date.thisMonthEnd
                        break;
                    default:
                        break;
                }
                this.$api.transformation.getLogUserCoinListBySC(data)
                .then(res=>{
                    console.log(res);
                    setTimeout(() => {
                       this.list = res.logUserCoinList
                    }, 1200);
                })
            },

            // 金额增加
            money_addend(num){
                this.money_tabs = num
                if(num){
                    this.money =  this.money *1 +  this.add_list[num]
                } else {
                    if(this.is){
                        this.money = parseInt(this.UserBaseInfo.balance)
                    }else{
                        var price = parseInt(this.actions[this.select].price)
                        if ( price >= 0){
                            this.money = price
                        }else{
                            this.money = 0
                        }
                    }
                }
            },

            // 选项选择
            onSelect(item) {
            // 可以通过 close-on-click-action 属性开启自动收起
                this.show = false;
                this.select = item.index
                this.$toast(`切换到 => ${item.name}`);
            },

            // 取消
            onCancel() {
                this.$toast('取消');
            },

            // 个人信息
            getUserBaseInfo(isref){
                if(isref){
                    this.$toast('正在刷新')
                }
                const {uid, token} = this.config
                var data = {
                    uid,
                    token,
                }
                this.$store.dispatch(`Game/${GAME_GET_USER_BASE_INFO}`, data)
                .then(()=>{
                    if(isref){
                        this.$toast('刷新完成')
                        $('.ico-ref').removeClass('acti')
                    }
                })
            },

            // 君博余额
            getMyBalance(){
                this.$api.transformation.getMyBalance(this.config)
                .then(res=>{
                    var dex = this.getIndex('1000001350416946')
                    if(res.result == 1 ){
                        this.gameList[dex].money = res.data >= 0 ? res.data : '获取失败'
                    }
                })
            },

            // 开元余额
            getAllAmount(isref){
                if(isref){
                    this.$toast('正在刷新')
                }
                this.$api.transformation.getAllAmount(this.config)
                .then(res=>{
                    if(isref){
                        this.$toast('刷新完成')
                        $('.ico-ref').removeClass('acti')
                    }
                    var dex = this.getIndex('1000001359742941')
                    this.gameList[dex].money = res.chessAmount >= 0 ? res.chessAmount : '获取失败'
                })
            },

            // VG余额
            getVgAmount(){
                this.$api.transformation.getVgAmount(this.config)
                .then(res=>{
                    console.log(res);
                    var dex = this.getIndex('1000000470622605')
                    this.gameList[dex].money = res.data >= 0 ? res.data : '获取失败'
                })
            },

            // FG余额
            getLyAmount(){
                this.$api.transformation.getLyAmount(this.config)
                .then(res=>{
                    console.log(res);
                    var dex = this.getIndex('1000001350416944')
                    this.gameList[dex].money = res.data >= 0 ? res.data : '获取失败'
                })
            },

            // 列表
            getGameList(){
                this.$api.transformation.getGameList()
                .then(res=>{
                    console.log(res);
                    let modity = res.data
                    modity.forEach(item=>{
                        item.money = 0
                    })
                    this.gameList = modity;
                    this.getMyBalance();
                    this.getAllAmount();
                    this.getVgAmount();
                    this.getLyAmount()
                })
            },

            getIndex(code){
                var dex = -1;
                this.gameList.forEach((item, index)=>{
                    if(item.gameCode == code){
                        dex = index
                    }
                })
                return dex
            },
            // 确实转换
            to_ky(){
                var current = this.actions[this.select]
                console.log(current);
                console.log(this.is);
                if(this.money==''){
                    this.$toast('请输入金额');
                    return false;
                }else{
                    if(this.is){
                        if(this.UserBaseInfo.balance >= this.money){
                            this.to_change(current.code, true, false);
                        }else{
                            this.$toast('转入的金额不能大于自己的余额');
                        }

                    }else{
                        if(current.price >= this.money){
                            this.to_change(current.code, false, false);
                        }else{
                            this.$toast('转入的金额不能大于游戏的余额');
                        }
                    }
                }

            },

            // 开元棋牌
            kaiyuan(up, all){
                let data = {
                    ...this.config
                }
                if(up){
                    if(this.UserBaseInfo.balance > 0){
                        this.$toast.loading({
                            duration: 0,
                            forbidClick: true,
                            message: '转换中...',
                        })
                        if(all){
                            data.userBalance = this.UserBaseInfo.balance
                        }else{
                            data.userBalance = this.money
                        }
                        // 上分
                        this.$api.transformation.changeAmount(data)
                        .then(res=>{
                            this.$toast(res.description);
                            this.getAllAmount();
                            this.getUserBaseInfo();
                        })
                    }else{
                        this.$toast('余额不足');
                    }
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.chessAmount = this.money
                    // 下分
                    this.$api.transformation.unChangeAmount(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getAllAmount();
                        this.getUserBaseInfo();
                    })
                }
            },

            // 君博
            junbo(up, all){
                let data = {
                    ...this.config,
                    merchantName: companyShortName
                }
                if(up){
                    if(this.UserBaseInfo.balance > 0){
                        this.$toast.loading({
                            duration: 0,
                            forbidClick: true,
                            message: '转换中...',
                        })
                        if(all){
                            data.money = this.UserBaseInfo.balance;
                        }else{
                            data.money = this.money;
                        }
                        // 上分到CP
                        this.$api.transformation.upToJb(data)
                        .then(res=>{
                            this.$toast(res.description);
                            this.getUserBaseInfo();
                            this.getMyBalance();
                        })
                    }else {
                        this.$toast('余额不足');
                    }
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.money = this.money;
                    // 从CP下分
                    this.$api.transformation.downFromJb(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getMyBalance();
                        this.getUserBaseInfo();
                    })
                }
            },

            // VG转换
            vg(up, all){
                console.log(up, all);
                let data = {
                    ...this.config
                }
                if(up){
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    if(all){
                        data.money = parseInt(this.UserBaseInfo.balance);
                    }else{
                        data.money = this.money;
                    }
                    this.$api.transformation.upToVg(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getVgAmount();
                        this.getUserBaseInfo();
                    })
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.money = this.money;
                    this.$api.transformation.downVg(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getVgAmount();
                        this.getUserBaseInfo();
                    })
                }
            },

            fg(up, all){
                console.log(up);
                console.log(up, all);
                let data = {
                    ...this.config
                }
                if(up){
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    if(all){
                        data.money = parseInt(this.UserBaseInfo.balance);
                    }else{
                        data.money = this.money;
                    }
                    this.$api.transformation.upToFg(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getLyAmount();
                        this.getUserBaseInfo();
                    })
                }else{
                    this.$toast.loading({
                        duration: 0,
                        forbidClick: true,
                        message: '转换中...',
                    })
                    data.money = this.money;
                    this.$api.transformation.downFg(data)
                    .then(res=>{
                        this.$toast(res.description);
                        this.getLyAmount();
                        this.getUserBaseInfo();
                    })
                }
            }
        },
        filters: {
            operType(val){
                var str =''
                switch (val) {
                    case 26:
                        str = '开元转余额'
                        break;
                    case 27:
                        str = '余额转开元'
                        break;
                    case 38:
                        str = '余额转乐游'
                        break;
                    case 42:
                        str = '余额转VG'
                        break;
                    case 43:
                        str = 'VG转余额'
                        break;
                    case 56:
                        str = '彩票转余额'
                        break;
                    case 57:
                        str = '余额转彩票'
                        break;
                    default:
                        break;
                }
                return str
            }
        }
    }
</script>

<style>

</style>
